*{
    margin-top: 30px;
}
.heading{
    color: rgb(202, 35, 43);
    text-align: center;

}

body{
    background-color:#FCF4F0 ;
}
.side{
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-self:center;
}

.container{
    min-height: 250px;
    background-color: #C6CEBE; 
    border:solid 1.7px rgb(130, 139, 130);
    display:inline-block;
    width:45%;
    
}
.available{
    width:auto;
    display:flex;
    justify-content: center;
    align-items:center;
}
.selected{
    display:flex;
    justify-content: center;
    align-items:center;
}
.highlighted_1{
    border:solid 1.2px black;
    cursor: pointer;
    padding:5px;
    margin:3px;
    background-color: darken(#F6F7FB,10%);
&:hover {
    background-color: rgb(247, 240, 242)
}

}
.highlighted_2{
    border:solid 1.2px black;
    cursor: pointer;
    padding:5px;
    margin:3px;
    background-color: #BEEDE5;;

&:hover {
    background-color: rgb(247, 240, 242)
}

}

button{
    background:  #DBABB7;;
    width: 80px;
    padding: 4px 0;
    margin:10px;
    position: inherit;
    top :20%;
    left:20%;
    cursor:pointer;
    border-color:rgb(12, 11, 11);
    border-width: 0.05em;
    &:hover{
        background-color: darken(#DBABB7,10%);
    }
    &:focus{
        outline:none;
    }
   
}

